<script>
import { WOW } from 'wowjs'
import BMap from "BMap";
import axios from 'axios'; // 需要安装axios: npm install axios

export default {
  name: "ContactUs",
  data() {
    return {
      activeCompany: 'hongkong', // 添加公司切换状态，默认显示香港
      formData: {
        name: '',
        email: '',
        countryCode: '+86', // 新增国家代码字段，默认为中国
        phoneNumber: '',    // 新增电话号码字段
        phone: '',          // 保留原字段用于提交
        consulttopic: '',   // 修改为与后端匹配的字段名
        consultinfo: '',
        agreement: false
      },
      isSubmitting: false,
      submitMessage: '',
      submitStatus: false,
      activeQR: null, // 添加 activeQR 数据属性
      qrCodeData: {
        weixin: {
          title: '微信二维码',
          image: require('@/assets/img/wechat.png')
        },
        weibo: {
          title: '微博二维码',
          image: require('@/assets/img/wechat.png') // 替换为实际的微博二维码图片
        },
        xiaohongshu: {
          title: '小红书二维码',
          image: require('@/assets/img/wechat.png') // 替换为实际的小红书二维码图片
        }
      }
    };
  },
  computed: {
    getQRTitle() {
      return this.activeQR ? this.qrCodeData[this.activeQR].title : '';
    },
    getQRImage() {
      return this.activeQR ? this.qrCodeData[this.activeQR].image : '';
    }
  },
  methods: {
    showQRCode(type) {
      this.activeQR = type;
      // 添加body类防止滚动
      document.body.classList.add('modal-open');
    },
    closeQRCode() {
      this.activeQR = null;
      // 移除body类恢复滚动
      document.body.classList.remove('modal-open');
    },
    switchCompany(company) {
      this.activeCompany = company;
      // 切换公司后更新地图
      this.$nextTick(() => {
        this.updateMap();
      });
    },
    
    // 新增地图更新方法
    updateMap() {
      // 获取地图实例
      var map = new BMap.Map("map");
      map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
      
      // 根据当前选择的公司设置地图中心点
      let point;
      let address;
      let title;
      
      if (this.activeCompany === 'hongkong') {
        // 香港公司坐标
        point = new BMap.Point(114.224683, 22.290108);
        address = "香港鰂魚涌華蘭路20號華蘭中心7樓7室";
        title = "知命者文化产业有限公司（香港）";
      } else {
        // 厦门公司坐标
        point = new BMap.Point(118.090283, 24.472725);
        address = "厦门市思明区湖滨南路20号703室";
        title = "知命者文化产业有限公司（厦门）";
      }
      
      map.centerAndZoom(point, 18); // 初始化地图，设置中心点坐标和地图级别
      
      // 创建自定义尺寸的图标
      var myIcon = new BMap.Icon("https://api.map.baidu.com/images/marker_red.png", new BMap.Size(39, 45), {
        // 设置图标的偏移量，使其底部中心点对准坐标点
        anchor: new BMap.Size(19, 45),
        // 设置图标的大小，这里将默认图标放大约1.5倍
        imageSize: new BMap.Size(39, 45)
      });
      
      // 创建标注并使用自定义图标
      var marker = new BMap.Marker(point, {icon: myIcon});
      map.addOverlay(marker); // 将标注添加到地图中
      
      var opts = {
        width: 250, // 信息窗口宽度
        height: 80, // 信息窗口高度
        title: title // 信息窗口标题
      };
      var infoWindow = new BMap.InfoWindow(address, opts); // 创建信息窗口对象
      map.openInfoWindow(infoWindow, point); // 打开信息窗口
      
      // 添加点击标记事件
      marker.addEventListener("click", function(){
        map.openInfoWindow(infoWindow, point);
      });
    },
    
    // 提交表单方法
    submitForm() {
      // 表单验证
      if (!this.formData.name || !this.formData.email || !this.formData.consultinfo) {
        this.submitMessage = '请填写必填字段';
        this.submitStatus = false;
        return;
      }
      
      if (!this.formData.agreement) {
        this.submitMessage = '请同意个人信息收集声明';
        this.submitStatus = false;
        return;
      }
      
      // 合并国家代码和电话号码
      this.formData.phone = this.formData.phoneNumber ? (this.formData.countryCode + ' ' + this.formData.phoneNumber) : '';
      
      // 设置提交状态
      this.isSubmitting = true;
      this.submitMessage = '';
      
      // 准备提交数据，确保字段名称与后端匹配
      const contactData = {
        name: this.formData.name,
        email: this.formData.email,
        phone: this.formData.phone,
        consulttopic: this.formData.consulttopic,  // 确保使用正确的字段名
        consultinfo: this.formData.consultinfo,
        createdate: new Date().toISOString()
      };
      
      console.log('提交的数据:', contactData); // 添加日志便于调试
      
      // 使用axios发送表单数据到后端
      axios.post('http://localhost:8081/api/contact/submit', contactData)
        .then(response => {
          console.log('服务器响应:', response.data); // 添加日志
          if (response.data.success) {
            this.submitMessage = '您的咨询已成功提交，我们将尽快与您联系！';
            this.submitStatus = true;
            this.resetForm();
          } else {
            this.submitMessage = response.data.message || '提交失败，请稍后再试';
            this.submitStatus = false;
          }
        })
        .catch(error => {
          console.error('提交表单时出错:', error);
          if (error.response) {
            console.error('错误状态:', error.response.status);
            console.error('错误数据:', error.response.data);
          }
          this.submitMessage = '提交失败，请稍后再试或直接联系我们';
          this.submitStatus = false;
        })
        .finally(() => {
          this.isSubmitting = false;
        });
    },
    
    // 重置表单
    resetForm() {
      this.formData = {
        name: '',
        email: '',
        countryCode: '+86',  // 重置为默认值
        phoneNumber: '',     // 清空电话号码
        phone: '',           // 清空合并后的电话
        consulttopic: '',    // 修改为与后端匹配的字段名
        consultinfo: '',
        agreement: false
      };
    }
  },
  mounted() {
    // 设置当前导航索引为"联系我们"
    const contactUsIndex = 6; // 联系我们在导航列表中的索引
    sessionStorage.setItem('navIndex', contactUsIndex);
    
    // 确保页面从顶部开始显示
    window.scrollTo(0, 0);
    
    // 百度地图初始化 (保持不变)
    var map = new BMap.Map("map"); // 创建地图实例
    var point = new BMap.Point(114.224683,22.290108); // 创建点坐标
    map.centerAndZoom(point, 18); // 初始化地图，设置中心点坐标和地图级别
    map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
    var marker = new BMap.Marker(point); // 创建标注
    map.addOverlay(marker); // 将标注添加到地图中
    var opts = {
      width: 50, // 信息窗口宽度
      height: 10, // 信息窗口高度
      title: "知命者文化产业有限公司" // 信息窗口标题
    };
    var infoWindow = new BMap.InfoWindow(
      "香港鰂魚涌華蘭路20號華蘭中心7樓7室",
      opts
    ); // 创建信息窗口对象
    map.openInfoWindow(infoWindow, map.getCenter()); // 打开信息窗口
    
    // 初始化动画
    var wow = new WOW({
      boxClass: 'wow',
      animateClass: 'animated',
      offset: 100,
      mobile: true,
      live: true
    });
    wow.init();
  }
};
</script>

